

/* eslint-disable no-unused-vars */


import React, { Component } from "react"
import MetaTags from 'react-meta-tags';
import {
  Card,
  Col,
  Container,
  Row,
  CardBody,
  CardHeader,
  CardTitle,
  FormGroup,
  Label,
  Button,
  Form,
  Input,
  InputGroupAddon,
  InputGroupText,
  InputGroup,
  Breadcrumb,
  BreadcrumbItem,
} from "reactstrap"

import {
  CButton,
  CCard,
  CCardHeader,
  CCardBody,
  CCardFooter,
  CCol,
  CContainer,
  CForm,
  CInput,
  CInputGroup,
  CInputGroupPrepend,
  CInputGroupText,
  CRow
} from '@coreui/react'
import Select from "react-select"
import axios from 'axios'
import  { Redirect } from 'react-router-dom'

import { French } from "flatpickr/dist/l10n/fr.js";

// import Flatpickr from "react-flatpickr"
import Flatpickr from "flatpickr";

import DatePicker from "react-datepicker";
import { registerLocale, setDefaultLocale } from  "react-datepicker";
import es from 'date-fns/locale/es';
import $ from 'jquery';
import conf from '../../../conf.js';
import ReactSelect from 'react-select'
import makeAnimated from 'react-select/animated';
import ville from "../../dict/ville.js";
import { storage } from "../../../firebase/firebase";
import ModalMessage from "../../pages/messages/ModalMessage"
const animatedComponents = makeAnimated();


// const cities = require('all-the-cities');

registerLocale('es', es)

  const optionEtat = [
    {
      options: [
        { label: "Bien droul", value: "deroule" },
        { label: "Annul", value: "annule" },
        { label: "Repouss", value: "repousse" },
      ]
    },
  ]

class AjoutMatch extends Component {
    constructor(props) {
        super(props)
        this.state = {
          ok:"",
          equipe:[],
          tournoi:{},
          equipeA:{},
          equipeB:{},
          niveau:"",
          etat:"PROGRAMME",
          terrain:{},
          terrains:[],
          show:false,
          success:true,
          boutton:"OK",
          message:"Match ajout avec succs !!",
          titre:"Message ajout de match"
        }
        this.handleChangeNiveau.bind(this);
        this.handleChangeEquipeA.bind(this);
        this.handleChangeEquipeB.bind(this);
        this.handleChangeEtat.bind(this);
        this.handleChangeTerrain.bind(this);
        // this.handleChangeDate.bind(this);
        // this.handleChangeHeure.bind(this);
    }

    componentDidMount() {
      
     let  data=  JSON.parse(localStorage.getItem('token_auth'))
     var id=localStorage.getItem("idTournoi")
     let equipe=[]
     const requestOptions = {
       method: 'GET',
       headers: { 
         'Authorization':'Bearer '+data.token,
   
       },
     };
 
     fetch(conf.url+"tournoi/tournois/"+id,requestOptions)
     .then((response) => response.json())
     .then((data) => {
       console.log("data",data);
       $("#nom_tournoi").val(data.nom)
       this.setState({tournoi:data})
       data.equipes.map((element,idx)=>{
       this.setState({equipe:[...this.state.equipe,{label:element.nomEquipe,value:element}]})
       })
     
       })


       fetch(conf.url+"terrain/terrains",requestOptions)
       .then((response) => response.json())
       .then((data) => {
         console.log("data",data);
        
         data.map((element,idx)=>{
         this.setState({terrains:[...this.state.terrains,{label:element.nom,value:element}]})
         })
       
         })


   }

   handleAddMatch=()=>{
    var id=localStorage.getItem("idTournoi")
    let  token=  JSON.parse(localStorage.getItem('token_auth'))
    var date=""
    
    try{
      var data=$("#date_match").val()
      date= data.split("-")[2]+"/"+data.split("-")[1]+"/"+data.split("-")[0]
    }catch(err){

    }
    var data= {
      "scoreVisiteur": $("#scorevisiteur").val(),
      "scoreVisite": $("#scorevisite").val(),
      "niveauTournoi":this.state.niveau,
      "codeVideoEmded":$("#codevideo").val(),
      "equipeVisites": [this.state.equipeA],
      "equipeVisiteurs": [this.state.equipeB],
      "annule":"",
      "date":date,
      "heure":$("#heure_match").val(),
      "repousse":this.state.etat,
      "tournoi":null,
      "terrain":this.state.terrain,
      "reservation":null,
      "idTournoi":id

    }

    console.log("data a transmettre",data)

    const requestOptions = {
      method: 'POST',
      headers: { 'Content-Type': 'application/json','Accept': 'application/json',
      'Authorization':'Bearer '+token.token,
    },
      body: JSON.stringify(data)
  };
if(!this.state.equipeA.id || !this.state.equipeB.id){
  this.setState({show:true,success:false});
}else{
  if(!this.state.terrain.id){
    this.setState({show:true,success:false});
  }else{

    fetch(conf.url+"tournoi/add/match/"+id,requestOptions)
    .then((response) => response.json())
    .then((data) => {
       console.log("okkk====>",data)
      try{
         if(data.id){
          this.setState({show:true,success:true});
         }else{
          this.setState({show:true,success:false});
         }
      }catch(err){
        this.setState({show:true,success:false});
      }
       
    
     }
     
    );
  }
}
 
    
  } 


   handleChangeEquipeA = (e) => {
    console.log("adate =====>",e)
    try{
      this.setState({equipeA:e.value})
     
    }catch(ee){

    }
  }
    handleChangeEquipeB = (e) => {
      console.log("adate =====>",e)
      try{
        this.setState({equipeB:e.value})
       
      }catch(ee){
  
      }
    }

    handleChangeNiveau = (e) => {
      console.log("adate =====>",e)
      try{
        this.setState({niveau:e.value})
       
      }catch(ee){
  
      }
    }

    handleChangeEtat = (e) => {
      console.log("adate =====>",e)
      try{
        this.setState({etat:e.value})
       
      }catch(ee){
  
      }
    }

    handleChangeTerrain= (e) => {
      console.log("adate =====>",e)
      try{
        this.setState({terrain:e.value})
       
      }catch(ee){
  
      }
    }

    // handleChangeDate= (e) => {
    //   console.log("date =====>",$("#date_match").val())
    //   try{
    //     this.setState({date:e})
       
    //   }catch(ee){
  
    //   }
    // }
    // handleChangeHeure= (e) => {
    //   console.log("heure =====>",$("#heure_match").val())
    //   try{
    //     this.setState({heure:e})
       
    //   }catch(ee){
  
    //   }
    // }
    handleValidate=()=>{
      if(this.state.success){
        this.setState({ok: <Redirect to='/detail-tournoi'/>});
      }else{
        this.setState({show:false})
      }
      
    }
   
  render() {

    const { pick } = this.state
    const { selectedGroup } = this.state
    const { selectedLangue } = this.state
    const animatedComponents = makeAnimated();

    const optionEtat = [
      {
        options: [
          { label: "Programm", value: "PROGRAMME" },
          { label: "Jou", value: "JOUE" },
          { label: "Repouss", value: "REPOUSE" },
          { label: "Annul", value: "ANNULE" },
        ]
      },  
    ]

    const niveau = [
     
          { label: "16ime", value: "16ime" },
          { label: "8me", value: "8me" },
          { label: "4ime", value: "4ime" },
          { label: "2ime", value: "2ime" },
          { label: "Finale", value: "Finale" },
        
         ]


    
  return (
        <>
          <React.Fragment>
            <div className="page-content">
             {this.state.ok}
             <ModalMessage
              show={this.state.show}
              success={this.state.success}
              boutton={this.state.boutton}
              handleValidate={this.handleValidate}
              message={this.state.message}
              titre={this.state.titre}
              onCloseClick={() => this.setState({ show: false })}
              />

            <Container fluid={false} className="justify-content-center">

              <Row className="justify-content-center">
              <Col md="12" lg="12" xl="12">

                {/* <Row ClassName="mb-3">
                  <Col md="8">
                      <div className="page-title-box d-flex align-items-center justify-content-between">
                        <h4 className="mb-0 font-size-18">Nouveau compte</h4>
                      </div>
                  </Col>
                </Row> */}
     
                

                <Card ClassName="mt-5">
                  {/* <CCardHeader className="p-4"> */}
                    <h1 className="p-4" >Enregistr un  match</h1>
                  {/* </CCardHeader> */}


                  <CardBody>
                    <Row> 
                      <Col md="5" className="mt-2">
                        <label>Equipe  domicile</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeEquipeA}
                          options={this.state.equipe}
                          placeholder="Equipe  domicile"
                        />
                      </Col>
                      <Col md="1" className="mt-2">
                        <label>Score</label>
                        <Input
                          className="form-control"
                          type="number"
                          min="0"
                          id="scorevisite"
                          placeholder="00"
                        />
                      </Col>
                      <Col md="5" className="mt-2">
                        <label>Equipe visiteur</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeEquipeB}
                          options={this.state.equipe}
                          placeholder="Equipe visiteur"
                        />
                      </Col>
                      <Col md="1" className="mt-2">
                        <label>Score</label>
                        <Input
                          className="form-control"
                          type="number"
                          min="0"
                          id="scorevisiteur"
                          placeholder="00"
                        />
                      </Col>
                    </Row>
                    <Row className="mt-4">
                      <Col md="6" className="mt-2">
                        <label>Nom du tournoi</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="nom_tournoi"
                          readOnly
                        />
                      </Col>
                      <Col md="6" className="mt-2">
                        <label>Niveau du tournoi</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeNiveau}
                          options={niveau}
                          placeholder="Type d'entreprise"
                        />
                      </Col>
                    </Row>
                    <Row className="mt-4">
                      <Col md="4" className="mt-2">
                        <label>Nom du terrain</label>
                        <ReactSelect
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeTerrain}
                          options={this.state.terrains}
                          placeholder="Nom du terrain"
                        />
                      </Col>

                      <Col md="4" className="mt-2">
                        <label>Code vido</label>
                        <Input
                          className="form-control"
                          type="text"
                          id="codevideo"
                          placeholder="Code vido"
                        />
                      </Col>

                      <Col md="4" className="mt-2">
                        <label>Etat</label>
                        <ReactSelect
                          defaultValue={{ label:"Programm", value:"PROGRAMME" }}
                          closeMenuOnSelect={true}
                          components={animatedComponents}
                          onChange={this.handleChangeEtat}
                          options={optionEtat}
                          placeholder="Etat"
                        />
                      </Col>
                    </Row>

                    <Row className="mt-4">
                      <Col md="6" className="mt-2">
                        <label>Date du match</label>
                        <Input
                         
                          className="form-control"
                          type="date"
                          id="date_match"
                          placeholder="Date du match"
                         // onChange={this.handleChangeDate}
                        />
                      </Col>
                      <Col md="6" className="mt-2">
                        <label>Heure du match</label>
                        <Input
                          className="form-control"
                          type="time"
                          id="heure_match"
                          placeholder="Heure du match"
                         // onChange={this.handleChangeHeure}
                        />
                      </Col>
                    </Row>
                   
                    
                    <Row className="justify-content-start mt-5">
                      <Col md="6" className="mt-1">
                        <Button id="initial"  onClick={this.handleAddMatch} color="success" block className="b-lg" >
                          Ajouter le match
                        </Button>
                        {/* <Button id="chargement" color="info" className="b-lg" type="button" disabled>
                        <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"> </span>
                          Envoi...
                        </Button> */}
                      </Col>
                      <Col md="6" className="mt-1">
                        <Button  color="secondary" block className="b-lg">
                          Annuler
                        </Button>
                      </Col>
                    </Row>
                    
                  </CardBody>
                </Card>
              </Col>
            </Row>
          </Container>
        </div>
             
      </React.Fragment>
    </>
        )}

  }
  export default AjoutMatch